.loading-spinner {
    position: absolute;
    content: '';
    left: 45%;
    top: 25%;
    margin: auto;
    //   margin: 50px 0 50px 0;
    z-index: 999;
    width: 6rem;
    height: 6rem;
    display: inline;
    border: 7px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: #ff6b01;
    margin-bottom: 10rem;
    animation: 1s spin infinite ease-in-out;
  }
  
  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }
  
  .loading-dots > div {
    position: relative;
    content: '';
    left: 45%;
    top: 25%;
    margin: auto;
    z-index: 999;
    width: 25px;
    height: 25px;
    background-color: #ff6b01;
    border-radius: 50%;
    display: inline-block;
    animation: 3s bounce infinite ease-in-out both;
  }
  
  .loading-dots .bounce {
    animation-delay: 2s;
  }
  
  .loading-dots .bounce2 {
    animation-delay: 1s;
  }
  
  @keyframes bounce {
    0%,
    80%,
    100% {
      transform: scale(0);
    }
    40% {
      transform: scale(1);
    }
  }
  